.error {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background: transparent linear-gradient(120deg, #132056 0%, #0B539FCC 41%, #076BC180 65%, #0097FF1C 100%) 0% 0%;
}

.error img {
    height: 100%;
    margin-top: 7rem;
}